import React, { useEffect, useState } from 'react';
import API from '../api';

function AdminPanel() {
  // Gestion des utilisateurs ‚á©
  const [users, setUsers] = useState([]);
  const [clubs, setClubs] = useState([]);
  const [uerr, setUerr] = useState('');
  const [umsg, setUmsg] = useState('');

  // Media moderation ‚á©
  const [pendingMedia, setPendingMedia] = useState([]);
  const [merr, setMerr] = useState('');
  const [mmsg, setMmsg] = useState('');

  const fetchUsersAndClubs = async () => {
    try {
      setUerr(''); setUmsg('');
      const [uRes, cRes] = await Promise.all([
        API.get('/user'),      // GET (admin) liste users
        API.get('/clubs'),     // liste clubs
      ]);
      setUsers(Array.isArray(uRes.data) ? uRes.data : []);
      setClubs(Array.isArray(cRes.data) ? cRes.data : []);
    } catch (e) {
      setUerr(e.response?.data?.error || 'Erreur chargement utilisateurs/clubs');
    }
  };

  useEffect(() => {
    fetchUsersAndClubs();
  fetchPendingMedia();
  }, []);

  const userHasClub = (user, clubId) => {
    const list = user.managerClubs || [];
    return list.some((c) => (c.id || c) === String(clubId));
  };

  const toggleClubForUser = async (userId, clubId, checked) => {
    try {
      setUerr(''); setUmsg('');
      const action = checked ? 'add' : 'remove';
      await API.post(`/user/${userId}/manager-clubs`, { clubId, action });
      // M√†J locale
      setUsers((arr) => arr.map((u) => {
        if (u.id !== userId) return u;
        const has = userHasClub(u, clubId);
        if (checked && !has) {
          const club = clubs.find((c) => String(c._id) === String(clubId));
          return {
            ...u,
            managerClubs: [...(u.managerClubs || []), { id: String(clubId), clubName: club?.clubName }],
          };
        }
        if (!checked && has) {
          return {
            ...u,
            managerClubs: (u.managerClubs || []).filter((c) => (c.id || c) !== String(clubId)),
          };
        }
        return u;
      }));
      setUmsg('Droits mis √† jour');
    } catch (e) {
      setUerr(e.response?.data?.error || 'Erreur mise √† jour droits');
    }
  };

  const fetchPendingMedia = async () => {
    try {
      setMerr(''); setMmsg('');
      const { data } = await API.get('/media/pending');
      setPendingMedia(Array.isArray(data) ? data : []);
    } catch (e) {
      setMerr(e.response?.data?.error || 'Erreur chargement m√©dias en attente');
    }
  };

  const moderate = async (id, action) => {
    try {
      setMerr(''); setMmsg('');
      await API.patch(`/media/${id}/moderate`, { action });
      setPendingMedia((arr) => arr.filter((m) => m.id !== id));
      setMmsg(action === 'approve' ? 'Image approuv√©e' : 'Image rejet√©e');
    } catch (e) {
      setMerr(e.response?.data?.error || 'Erreur mod√©ration');
    }
  };

  return (
    <div>
      <h2>üéõÔ∏è Panel de contr√¥le Admin</h2>
      {uerr && <div style={{ color: 'crimson', marginBottom: 8 }}>{uerr}</div>}
      {umsg && <div style={{ color: 'seagreen', marginBottom: 8 }}>{umsg}</div>}

      <h2 style={{ marginTop: 24 }}>Gestion des utilisateurs</h2>
      <div style={{ overflowX: 'auto' }}>
        <table style={{ borderCollapse: 'collapse', minWidth: 720 }}>
          <thead>
            <tr>
              <th style={{ textAlign: 'left', borderBottom: '1px solid #ddd', padding: 8 }}>Utilisateur</th>
              <th style={{ textAlign: 'left', borderBottom: '1px solid #ddd', padding: 8 }}>R√¥le</th>
              <th style={{ textAlign: 'left', borderBottom: '1px solid #ddd', padding: 8 }}>Clubs g√©r√©s</th>
            </tr>
          </thead>
          <tbody>
            {users.map((u) => (
              <tr key={u.id}>
                <td style={{ borderBottom: '1px solid #eee', padding: 8 }}>{u.username}</td>
                <td style={{ borderBottom: '1px solid #eee', padding: 8 }}>{u.role}</td>
                <td style={{ borderBottom: '1px solid #eee', padding: 8 }}>
                  <div style={{ display: 'flex', flexWrap: 'wrap', gap: 10 }}>
                    {clubs.map((c) => {
                      const checked = userHasClub(u, c._id);
                      return (
                        <label key={c._id} style={{ display: 'inline-flex', alignItems: 'center', gap: 6 }}>
                          <input
                            type="checkbox"
                            checked={checked}
                            onChange={(e) => toggleClubForUser(u.id, c._id, e.target.checked)}
                          />
                          <span>{c.clubName}</span>
                        </label>
                      );
                    })}
                  </div>
                </td>
              </tr>
            ))}
            {users.length === 0 && (
              <tr><td colSpan={3} style={{ padding: 8, color: '#666' }}>Aucun utilisateur.</td></tr>
            )}
          </tbody>
        </table>
      </div>

      <h2 style={{ marginTop: 24 }}>Mod√©ration des images</h2>
      {merr && <div style={{ color: 'crimson', marginBottom: 8 }}>{merr}</div>}
      {mmsg && <div style={{ color: 'seagreen', marginBottom: 8 }}>{mmsg}</div>}
      <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fill, minmax(220px,1fr))', gap: 12 }}>
        {pendingMedia.map((m) => (
          <div key={m.id} style={{ border: '1px solid #ddd', borderRadius: 8, padding: 8 }}>
            <div style={{ fontSize: 12, color: '#666' }}>#{m.id.slice(-6)} ‚Ä¢ nsfwProb: {m.flags?.nsfwProb ?? '?'}</div>
            <div style={{ margin: '6px 0' }}>
              <img
                src={`${API.defaults.baseURL.replace(/\/$/,'')}/media/${m.id}/raw`}
                alt="pending"
                style={{ width: '100%', height: 140, objectFit: 'cover', borderRadius: 6 }}
              />
            </div>
            <div style={{ display: 'flex', gap: 8 }}>
              <button className="week-btn" onClick={() => moderate(m.id, 'approve')}>Approuver</button>
              <button className="week-btn" onClick={() => moderate(m.id, 'reject')}>Rejeter</button>
            </div>
          </div>
        ))}
        {pendingMedia.length === 0 && (
          <div style={{ color: '#666' }}>Aucune image en attente</div>
        )}
      </div>
    </div>
  );
}

export default AdminPanel;
